const testData = {
    animals: [animals1],
    city: [city1, city2, city3],
    nature: [nature1],
    arts: [art1, art2, art3, art4, art5, art6, art7, art8],
    people: [people1, people2, people3, people4, people5, people6, people7, people8]
}

const getAnimals = () => {
    return testData.animals
}

const getCity = () => {
    return testData.city
}

const getNature = () => {
    return testData.nature
}

const getPeople = () => {
    return testData.people
}

const getArts = () => {
    return testData.arts
}

const getAll = () => {
    return [
        ...getCity(),
        ...getPeople(),
        ...getAnimals(),
        ...getNature(),
        ...getArts()
    ]
}

