import React from 'react'

const Preoloader = (props) => {
    const { imgLoaded } = props;
    const styles = {
        width: '100%',
        height: '100vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        color: '#fafafa'
    }
    if(!imgLoaded) {
        return (
            <div style={styles}>
                <h1>Loading...</h1>
            </div>
        )
    } else {
        return props.children;
    }
}

export default Preoloader;