import React, { Component, Fragment } from 'react'
import './header.css'
import { Link } from 'react-router-dom'


export default class Header extends Component {

    state = {
        currentPage: '',
        modalOpened: false
    }
    
    render() {
        const linkData = [
            {to: '/', label: 'All'},
            {to: '/animals', label: 'Animals'},
            {to: '/nature', label: 'Nature'},
            {to: '/city', label: 'City'},
            {to: '/people', label: 'People'},
            {to: '/arts', label: 'Arts'}
        ]
        return (
            <Fragment>
                <div className="modal">
                    
                </div>
                <header>
                    <div className="profile">
                        <div className="avatar"></div>
                        <h2>Tomiris Kenzhebek</h2>
                        <p>Photographer, Artist, Architect</p>
                        <button onClick={() => window.open('https://instagram.com/iamtomiriis')}
                        >Send message</button>
                    </div>
                </header>
                <nav>
                    {
                        linkData.map((item, index) => {
                            const isActive = item.label === this.state.currentPage;
                            const className = isActive ? ('li active') : 'li'; 
                            return (
                                <Link 
                                    to={item.to} 
                                    className={className} 
                                    key={index}
                                    onClick={(e) => this.setState({currentPage: e.target.innerHTML})}
                                    >{item.label}</Link>
                            )
                        })
                    }
                </nav>
            </Fragment>
        )
    }
}
