import React, { Component } from 'react'
import Header from '../header/header'
import ErrorBoundry from '../error-boundry/'
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom'
import ImageContainer from '../imageContainer/ImageContainer'
import Preloader from '../preoloader/';
import {
    getAnimals,
    getCity,
    getPeople,
    getNature,
    getAll,
    getArts
 } from '../../services/portfolioService'

export default class App extends Component {

    state = {
        imagesLoaded: false
    }

    componentDidMount() {
        window.onload = () => {
            // console.log('heeey')
            this.setState({
                imagesLoaded: true
            })
        }
    }

    render() {
        return(
            <ErrorBoundry>
                <Preloader imgLoaded={this.state.imagesLoaded}>
                    <Router>
                        <Header/>
                        <Switch>
                            <Route path="/" exact>
                                <ImageContainer getData={getAll()}/>
                            </Route>
                            <Route path="/animals">
                                <ImageContainer getData={getAnimals()}/>
                            </Route>
                            <Route path="/nature">
                                <ImageContainer getData={getNature()}/>
                            </Route>
                            <Route path="/city">
                                <ImageContainer getData={getCity()}/>
                            </Route>
                            <Route path="/people">
                                <ImageContainer getData={getPeople()}/>
                            </Route>
                            <Route path="/arts">
                                <ImageContainer getData={getArts()}/>
                            </Route>
                            <Redirect to="/" />
                        </Switch>
                    </Router>
                </Preloader>
            </ErrorBoundry>
        )
    }
    
}